const { io } = require('socket.io-client');
const logger = require('./logger');

class SocketClient {
  constructor(config, tradeExecutor, notificationService) {
    this.config = config;
    this.tradeExecutor = tradeExecutor;
    this.notificationService = notificationService;
    this.socket = null;
    this.isConnected = false;
    this.reconnectAttempts = 0;
  }

  // Socket.IO
  connect() {
    logger.info('Socket.IO', { url: this.config.url });

    this.socket = io(this.config.url, {
      reconnection: true,
      reconnectionDelay: this.config.reconnectDelay,
      reconnectionAttempts: this.config.maxReconnectAttempts,
      timeout: 10000
    });

    this.setupEventHandlers();
  }

  // 
  setupEventHandlers() {
    // 
    this.socket.on('connect', () => {
      this.isConnected = true;
      this.reconnectAttempts = 0;
      logger.info('Socket.IO', {
        socketId: this.socket.id,
        url: this.config.url
      });
    });

    // 
    this.socket.on('disconnect', (reason) => {
      this.isConnected = false;
      logger.warn('Socket.IO', {
        reason,
        socketId: this.socket.id
      });
    });

    // 
    this.socket.on('connect_error', (error) => {
      this.reconnectAttempts++;
      logger.error('Socket.IO', {
        error: error.message,
        attempts: this.reconnectAttempts,
        maxAttempts: this.config.maxReconnectAttempts
      });

      // 
      if (this.reconnectAttempts >= this.config.maxReconnectAttempts) {
        this.notificationService.sendErrorNotification(
          'Socket',
          `${this.reconnectAttempts}`
        );
      }
    });

    // 
    this.socket.on('reconnect_attempt', (attemptNumber) => {
      logger.info('Socket.IO', {
        attempt: attemptNumber,
        maxAttempts: this.config.maxReconnectAttempts
      });
    });

    // 
    this.socket.on('reconnect', (attemptNumber) => {
      this.isConnected = true;
      this.reconnectAttempts = 0;
      logger.info('Socket.IO', {
        attempts: attemptNumber,
        socketId: this.socket.id
      });
    });

    // 
    const trade_map = ['open', 'add', 'reduce', 'close'];
    for (let action of trade_map) {
      this.socket.on(`trade:${action}`, async (data) => {
        await this.handleTradeSignal(data.trade);
      });
    }


    // 
    this.socket.onAny((eventName, ...args) => {
      // 
    });
  }

  // 
  async handleTradeSignal(data) {
    try {
      logger.info('', data);

      const { quantity: amount, type, side, timestamp, reason } = data;
      // 
      if (timestamp) {
        const now = Date.now();
        const signalTime = new Date(timestamp).getTime();
        const delay = now - signalTime;

        if (delay > 30000) { // 30
          logger.warn('', {
            delay: `${delay}ms`,
            direction
          });
          return;
        }
      }

      // 
      let result;
      if (type === 'OPEN') {
        result = await this.tradeExecutor.open(side, amount, reason);
      }
      if (type === 'ADD') {
        result = await this.tradeExecutor.add(side, amount, reason);
      }
      if (type === 'REDUCE') {
        result = await this.tradeExecutor.reduce(side, amount, reason);
      }

      if (type === 'CLOSE') {
        result = await this.tradeExecutor.close();
      }

      logger.info('', {
        direction,
        success: result.success,
        error: result.error
      });

    } catch (error) {
      logger.error('', {
        data,
        error: error.message
      });

      await this.notificationService.sendErrorNotification(
        '',
        error.message
      );
    }
  }

  // 
  disconnect() {
    if (this.socket) {
      logger.info('Socket.IO');
      this.socket.disconnect();
      this.isConnected = false;
    }
  }

  // 
  getConnectionStatus() {
    return {
      connected: this.isConnected,
      socketId: this.socket?.id || null,
      reconnectAttempts: this.reconnectAttempts
    };
  }

  // 
  updateConfig(config) {
    this.config = { ...this.config, ...config };

    if (this.socket) {
      this.disconnect();
      setTimeout(() => {
        this.connect();
      }, 1000);
    }

    logger.info('Socket', this.config);
  }
}

module.exports = SocketClient;